*{color:#242424;text-decoration:none;-webkit-tap-highlight-color:transparent;font-family:'Jost', serif}html{margin-top:2rem;padding-bottom:3rem;display:flex;flex-direction:column;align-items:center;background:#dedede url(/rick-morty/static/media/background-light.d90701c5.jpg) no-repeat fixed}.Nav{display:flex;flex-direction:column;justify-content:center;flex-wrap:nowrap;align-items:center;align-content:center;margin-bottom:3rem}.Nav img{width:10rem;margin-bottom:1rem}.Nav h1{font-size:2.2rem;margin:0;text-transform:uppercase;font-weight:800;text-align:center}.Nav h2{color:#797979;font-size:1.2rem;margin-top:0}.Nav a{font-size:1.3rem}.Nav a:hover{color:#ff8400}.CharacterListContainer{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around}.SmallCard{display:flex;flex-direction:row;justify-content:space-between;background:white;width:28vw;border-radius:30px;overflow:hidden;margin-bottom:4rem;box-shadow:rgba(0,0,0,0.15) 0 4px 6px -1px,rgba(0,0,0,0.06) 0px 2px 4px -1px;height:10rem}@media (max-width: 500px){.SmallCard{justify-content:center;height:22rem;width:16rem;flex-direction:column}}@media (min-width: 501px) and (max-width: 1300px){.SmallCard{justify-content:center;height:22rem;width:16rem;flex-direction:column}}.SmallCard:hover{transform:scale(1.01);transition:0.2s}.SmallCard a{display:flex;width:100%;overflow:hidden}.SmallCard img{width:100%;opacity:1;transition:opacity 0.5s ease 0s}@media (max-width: 500px){.SmallCard img{height:100%}}@media (min-width: 501px) and (max-width: 1300px){.SmallCard img{height:100%}}.SmallCard h3{width:20rem;position:relative;padding-left:25px;font-size:1.6rem;font-weight:800;margin:1rem 1rem 0 0}@media (max-width: 500px){.SmallCard h3{margin-top:2rem}}@media (min-width: 501px) and (max-width: 1300px){.SmallCard h3{margin-top:2rem}}.SmallCard p{font-size:1.1rem;padding-left:25px;font-weight:normal}.SmallCard .textField{justify-content:start}@media (max-width: 500px){.SmallCard .textField{height:16rem}}@media (min-width: 501px) and (max-width: 1300px){.SmallCard .textField{height:16rem}}.SmallCard .statusField{padding-left:25px;height:2rem;display:flex;align-items:center;padding-top:1rem}.SmallCard .characteristics{font-size:0.9rem !important;color:#acacac !important;margin-bottom:-1rem}.SmallCard .id{font-size:0.8rem !important;color:#acacac !important;margin-left:0.5rem;font-weight:normal}.smallCardStatus,.cardAlive,.cardDead,.cardunknown{width:2rem;height:100%}@media (max-width: 500px){.smallCardStatus,.cardAlive,.cardDead,.cardunknown{width:100%;height:1rem}}@media (min-width: 501px) and (max-width: 1300px){.smallCardStatus,.cardAlive,.cardDead,.cardunknown{width:100%;height:1rem}}.cardAlive{background:#78b56e}.cardDead{background:#d63d2e}.cardunknown{background:#b3b3b3}.Card{visibility:visible;display:flex;flex-direction:column;align-items:center;justify-items:center;text-align:center;border:solid 6px #ffffff;padding:10px;background-image:url(/rick-morty/static/media/card.455c917a.png);background-position:center;background-size:cover;width:25rem;height:40rem;border-radius:60px;overflow:hidden;margin-bottom:4rem;box-shadow:rgba(0,0,0,0.5) 3px 4px 15px -1px,rgba(0,0,0,0.2) 0px 2px 4px -1px}.Card:hover{transform:scale(1.002);transition:0.2s}.Card h3{font-size:1.6rem;font-weight:bolder;text-transform:uppercase}.Card img{transform:scale(0.8);border-radius:50% !important;margin-bottom:-1rem}.Card p{font-size:1.1rem;margin:0.4rem}.Card .characteristics{font-size:0.9rem !important;color:#6b6b6b !important;margin-bottom:-0.5rem}.Card .statusField{margin-left:-1.5rem;padding-left:25px;display:flex;align-items:center}.Card .flex-container{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:center;align-content:center;align-items:flex-start}.Card .flex-item{text-align:center;order:0;flex:0 1 auto;align-self:auto}.CardControlsContainer{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-between;align-content:stretch;align-items:center}.PreviousPage,.NextPage{margin:4rem;order:0;flex:0 1 auto;align-self:auto;width:3rem;height:3rem;border-radius:50%;overflow:hidden;opacity:80%}.PreviousPage:hover,.NextPage:hover{transform:scale(0.9);transition:0.1s}.PreviousPage img,.NextPage img{width:100%}.Alive{height:0.7rem;width:0.7rem;margin-right:0.3rem;background:#78b56e;border-radius:50%}.Dead{height:0.7rem;width:0.7rem;margin-right:0.3rem;background:#d63d2e;border-radius:50%}.unknown{height:0.7rem;width:0.7rem;margin-right:0.3rem;background:#b3b3b3;border-radius:50%}.loaderText{margin-top:3rem;font-size:1.2rem;text-align:center;font-weight:normal}.loader,.loader:before,.loader:after{border-radius:50%;width:2.5em;height:2.5em;animation-fill-mode:both;animation:loading 0.7s infinite ease-in-out;font-size:0.5rem;margin:auto;position:relative;transform:translateZ(0);animation-delay:-0.16s}.loader p,.loader:before p,.loader:after p{font-size:2rem}.loader:before{content:'';position:absolute;top:0;left:-3.5em;animation-delay:-0.32s}.loader:after{content:'';position:absolute;top:0;left:3.5em}@keyframes loading{0%,80%,100%{box-shadow:0 2.5em 0 -1.3em}40%{box-shadow:0 2.5em 0 0}}.CardLoader,.CardLoader:after{border-radius:50%;width:10em;height:10em}.CardLoader{z-index:-5;margin:60px auto;font-size:10px;position:absolute;left:47%;text-align:center;border-top:1.1em solid rgba(255,255,255,0);border-right:1.1em solid rgba(255,255,255,0);border-bottom:1.1em solid rgba(255,255,255,0);border-left:0.6rem solid #303030;transform:translateZ(0);animation:loading2 0.5s infinite linear}@keyframes loading2{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

